import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-clone-edit',
  templateUrl: './clone-edit.component.html',
  styleUrls: ['./clone-edit.component.css']
})
export class CloneEditComponent implements OnInit {

  clone = {};

  constructor(private http: HttpClient, private router: Router, private route: ActivatedRoute) { }

  ngOnInit() {
    this.getClone(this.route.snapshot.params['id']);
  }

  getClone(id) {
    this.http.get('/clones/'+id).subscribe(data => {
      this.clone = data;
    });
  }

  updateClone(id) {
    this.http.put('/clones/'+id, this.clone).subscribe(res => {
          let id = res['id'];
          this.router.navigate(['/clone']);
        }, (err) => {
          console.log(err);
        }
      );
  }

}



