import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-clone-detail',
  templateUrl: './clone-detail.component.html',
  styleUrls: ['./clone-detail.component.css']
})
export class CloneDetailComponent implements OnInit {

  clone = {};

  constructor(private router: Router, private route: ActivatedRoute, private http: HttpClient) { }

  ngOnInit() {
    this.getCloneDetail(this.route.snapshot.params['id']);
  }

  getCloneDetail(id) {
    this.http.get('/clones/'+id).subscribe(data => {
      this.clone = data;
    });
  }


  deleteClone(id) {
    this.http.delete('/clones/'+id).subscribe(res => {
        this.router.navigate(['/clone']);
      }, (err) => {
        console.log(err);
    });
  }


}



