import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AppComponent } from './app.component';
import { CloneComponent } from './clone/clone.component';
import { FormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { CloneDetailComponent } from './clone-detail/clone-detail.component';
import { CloneCreateComponent } from './clone-create/clone-create.component';
import { CloneEditComponent } from './clone-edit/clone-edit.component';

const appRoutes: Routes = [
  {
    path: 'clone',
    component: CloneComponent,
    data: { title: 'Lista de Clones' }
  },
  {
    path: 'clone-detail/:id',
    component: CloneDetailComponent,
    data: { title: 'Detalhes do Clone' }
  },
  {
    path: 'clone-create',
    component: CloneCreateComponent,
    data: { title: 'Criar Clone' }
  },
  {
    path: 'clone-edit/:id',
    component: CloneEditComponent,
    data: { title: 'Editar Clone' }
  },
  { path: '',
    redirectTo: '/clone',
    pathMatch: 'full'
  }
];

@NgModule({
  declarations: [
    AppComponent,
    CloneComponent,
    CloneDetailComponent,
    CloneCreateComponent,
    CloneEditComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpClientModule,
    RouterModule.forRoot(
      appRoutes,
      { enableTracing: true }
    )
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



